<router-link class="rounded overflow-hidden bg-white shadow d-block position-relative h-100" :to="`/activities/${activity.ActivityID}`" :title="activity.ActivityName"><img class="position-absolute link" src="@/assets/icons/link_icon.svg" alt="link icon"><img class="object-cover w-100" v-if="'PictureUrl1' in activity.Picture" :src="activity.Picture.PictureUrl1" :alt="activity.Picture.PictureDescription1" height="163"><img class="object-cover w-100" v-else src="@/assets/images/illustration.png" alt="該資料沒有圖片" title="該資料沒有圖片" height="163"><div class="px-3 py-2"><h4 class="text-dark text-truncate">{{ activity.ActivityName }}</h4><div class="vstack"><div class="open-time mb-2 text-truncate" :title="`${start} ~ ${end}`">{{ start }} ~ {{ end }}</div><div class="location me-3 text-truncate" :title="activity.Location">{{ activity.Location }}</div></div></div></router-link>